.descriptionbox {
    margin: 120px 170px;
}

.descriptionboxnavigator{
    display: flex;
}

.description-nav-box{
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 16px;
    font-weight: 600;
    width: 171px;
    height: 70px;
    border: 1px solid #d0d0d0;
}

.description-nav-fade{
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid #d0d0d0;
    /* background-color: #fbfbfb; */
    font-size: 16px;
    font-weight: 600;
    width: 160px;
    color: #555;
}   

.descriptionbox-description{
    display: flex;
    flex-direction: column;
    gap: 25px;
    border: 1px solid #D0D0D0;
    padding: 48px;
    padding-bottom: 70px;
}