.chopcategory-banner {
    display: block;
    margin: 30px auto;
    width: 82%;
}

.shopcategory-indexsort {
    display: flex;
    margin: 0px 170px;
    justify-content: space-between;
    align-items: center;
}

.shopcategory-sort {
    padding: 10px 20px;
    border-radius: 40px;
    border: 1px solid #8888;
}

.shopcategory-indexsort p span {
    font-weight: 600;
}

.category-products {
    /* margin: 20px 170px; */
    margin-top: 30px;
    display: grid;
    grid-template-columns:repeat(4, 1fr);
    place-items: center;
    row-gap: 80px;
}

.shopcategory-loadmore {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 150px auto;
    width: 223px;
    height: 70px;
    border-radius: 75px;
    background: #332c2c;
    color: #787878;
    font-size: 18px;
    font-weight: 500;
    cursor: pointer;
}
